body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  padding: 5px;
}

ol {
  cursor: pointer;
  text-align: left;
}

.BlockAddBock {
  position: relative;
}

.AddNewBook {
  cursor: pointer;
}

.Remove {
  cursor: pointer;
  text-align: right;
  margin-right: 10px;
  color: gray;
}

.Remove:hover {
  color: black;
}

.AddNewBook:hover {
  color: rgb(84, 84, 84);
}

.NewId { 
  color: gray;
  text-align: right;
  margin-right: 10px;
  font-size: 16px;
}

.overBlock {
  background-color: black;
  opacity: 0.3;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 999;
}

.FormAddBook {
  position: absolute;
  border-radius: 5px;
  top: -90px;
  width: 100%;
  background-color: #18d8d1;
  z-index: 9999;
}

.NameButton {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.RenderList {
  max-width: 500px;
  background-color: rgb(42, 220, 214);
  padding: 5px;
  border-radius: 5px;
}

.componentsShow {
  text-align: center;
  background-color: rgb(35, 194, 189);
  margin-left: 5px;
  padding: 5px 5px 15px 15px;
  border-radius: 5px;
}

.timerSpan {
  margin: 0 10px 0 10px;
}

.UlToDo {
  padding: 0;
  margin: 0;
  list-style: none;
}

.UlToDo li {
  margin-bottom: 5px;
}

.Done {
  display: flex;
  justify-content: space-between;
  text-align: center;
  align-items: center;
  border-radius: 5px;
  background-color: #18d8d1;
  padding: 0 5px 0 10px;
  color: gray;
}

.Done i {
  text-decoration: line-through;
	text-decoration-color: rgb(198, 198, 198);
}

i {
  text-align: left;
}

.NotDone {
  display: flex;
  justify-content: space-between;
  text-align: center;
  align-items: center;
  border-radius: 5px;
  background-color: #18d8d1;
  padding: 0 5px 0 10px;
}

.buttonAddTask {
  margin-bottom: 10px;
}

button {
  cursor: pointer;
}


.StatusToDo {
  cursor: pointer;
}


.ToDoTaskP {
  cursor: pointer;
  color: gray;
}

.ToDoTaskP:hover {
  color: black;
}

.Description {
  width: 90%;
  padding: 10px;
  margin: 0;
  list-style: none;
  font-size: 14px;
  color: white;
  text-align: left;
  border-radius: 5px;
  border: 1px solid white;
}

.Description li:first-child {
  font-size: 14px;
  font-weight: bold;
}